.animated {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; }

.rotating {
  -webkit-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite; }

@-webkit-keyframes right {
  0% {
    -webkit-transform: translate(-15px); }
  50% {
    -webkit-transform: translate(15px); }
  100% {
    -webkit-transform: translate(-15px); } }

@-webkit-keyframes left {
  0% {
    -webkit-transform: translate(15px); }
  50% {
    -webkit-transform: translate(-15px); }
  100% {
    -webkit-transform: translate(15px); } }

@keyframes right {
  0% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px); }
  50% {
    -webkit-transform: translate(15px);
            transform: translate(15px); }
  100% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px); } }

@keyframes left {
  0% {
    -webkit-transform: translate(15px);
            transform: translate(15px); }
  50% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px); }
  100% {
    -webkit-transform: translate(15px);
            transform: translate(15px); } }

@-webkit-keyframes up {
  0% {
    margin-top: 5px;
    height: 10px; }
  50% {
    margin-top: -15px;
    height: 35px; }
  100% {
    margin-top: 5px;
    height: 10px; } }

@-webkit-keyframes down {
  0% {
    margin-top: -5px;
    height: 10px; }
  50% {
    margin-top: 15px;
    height: 25px; }
  100% {
    margin-top: -5px;
    height: 10px; } }

@keyframes up {
  0% {
    margin-top: 5px;
    height: 10px; }
  50% {
    margin-top: -15px;
    height: 35px; }
  100% {
    margin-top: 5px;
    height: 10px; } }

@keyframes down {
  0% {
    margin-top: -5px;
    height: 10px; }
  50% {
    margin-top: 15px;
    height: 25px; }
  100% {
    margin-top: -5px;
    height: 10px; } }

